/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.background {
  background-color: black; }

.container-one {
  align-self: center;
  margin-bottom: 0px; }

h1 {
  font-family: "Open Sans", sans-serif;
  font-weight: lighter;
  color: #ffffff;
  font-size: 14px;
  text-align: center;
  font-style: normal;
  font-weight: normal;
  padding-bottom: 20px; }

.header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  height: 500px;
  padding: 3rem;
  transition: all 1s ease-in-out 0s; }
  .header.after-header {
    height: 350px;
    padding-top: 0;
    transition: all 1s ease-in-out 0s; }
  @media only screen and (min-width: 600px) {
    .header {
      height: 500px;
      flex-direction: row;
      transition: all 1s ease-in-out 0s; }
      .header.after-header {
        padding: 5px;
        height: 140px;
        flex-direction: row;
        transition: all 1s ease-in-out 0s; } }
  @media only screen and (min-width: 1000px) {
    .header {
      height: 500px;
      display: flex;
      flex-direction: column;
      align-content: initial;
      transition: all 1s ease-in-out 0s; }
      .header.after-header {
        padding: 5px;
        height: 140px;
        display: flex;
        flex-direction: column;
        align-content: initial;
        padding-left: 0px;
        transition: all 1s ease-in-out 0s; } }

.logo {
  width: 500px;
  text-align: center;
  align-self: center;
  display: block;
  margin-bottom: 7rem; }
  .logo.after-logo {
    height: 150px; }
  @media only screen and (min-width: 600px) {
    .logo {
      height: 200px;
      width: auto;
      display: flex;
      align-self: center;
      flex-direction: row;
      transition: all 1s ease-in-out 0s; }
      .logo.after-logo {
        height: 70px;
        width: 100%;
        display: flex;
        flex-direction: row;
        margin-bottom: 10px;
        transition: all 1s ease-in-out 0s; } }
  @media only screen and (min-width: 1000px) {
    .logo {
      height: 200px;
      width: 100%;
      display: flex;
      flex-direction: row;
      padding-right: 70px;
      transition: all 1s ease-in-out 0s; }
      .logo.after-logo {
        height: 70px;
        width: 100%;
        display: flex;
        flex-direction: row;
        margin-bottom: 10px;
        transition: all 1s ease-in-out 0s; } }

.myselect {
  align-items: center;
  font-size: 1.1rem;
  width: 150px;
  height: 30px; }
  .myselect .myselect2 {
    display: inline-block;
    text-align: center; }
  @media only screen and (min-width: 600px) {
    .myselect {
      align-self: center;
      font-size: 1.1rem;
      display: flex;
      width: 130px;
      height: 30px;
      transition: all 1s ease-in-out 0s; } }
  @media only screen and (min-width: 1000px) {
    .myselect {
      align-items: flex-end;
      font-size: 1.1rem;
      display: block;
      width: 130px;
      height: 30px;
      transition: all 1s ease-in-out 0s; } }
  @media (max-width: 600px) {
    .myselect.after-select {
      height: 30px; } }
  @media only screen and (min-width: 600px) {
    .myselect.after-select {
      height: 25px;
      flex-direction: row;
      transition: all 1s ease-in-out 0s; } }
  @media only screen and (min-width: 1000px) {
    .myselect.after-select {
      height: 25px;
      display: flex;
      flex-direction: column;
      align-content: initial;
      transition: all 1s ease-in-out 0s; } }

ul {
  display: flex;
  flex-wrap: wrap;
  max-width: 500px; }
  ul li {
    display: flex; }
  ul a {
    color: white;
    text-decoration: none;
    width: 100%;
    height: 100%; }
  ul .paragraph {
    color: white;
    text-align: center;
    background: rgba(0, 0, 0, 0.7);
    margin-top: 300px;
    font-size: 18px;
    opacity: 0;
    transition: opacity 0.5s; }

style {
  width: 100%;
  height: 100%;
  justify-content: center;
  display: flex; }

.image {
  background-size: cover;
  display: flex;
  width: 100%;
  height: 100%; }

.image:hover .paragraph {
  opacity: 100;
  transition: opacity 1s; }

.image2 {
  justify-content: center;
  align-self: flex-end;
  top: 20px;
  width: 100%; }

.text {
  background: rgba(0, 0, 0, 0.6);
  bottom: 0;
  display: flex; }

.load {
  height: 80px;
  position: fixed;
  width: 80px;
  bottom: 50%;
  left: calc(50% - 50px);
  background: url("../../images/ajax-loader.gif");
  z-index: 9999;
  background-size: cover; }

.home {
  max-width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  padding-left: 0; }
  @media only screen and (min-width: 600px) {
    .home {
      max-width: 100%;
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 1fr 1fr; } }
  @media only screen and (min-width: 1000px) {
    .home {
      max-width: 100%;
      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-auto-rows: 1fr 1fr 1fr 1fr; } }

.footer {
  color: gray;
  font-size: 1rem;
  text-align: center;
  padding: 0 2rem;
  margin-bottom: 20px;
  padding-top: 40px; }
  @media only screen and (min-width: 1000px) {
    .footer {
      text-align: left; } }
